.chat-window-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #0A0E27;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 59px;
  padding: 10px 16px;
  background: #1E293B;
  flex-shrink: 0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.3s ease;
}
.chat-header .header-left {
  display: flex;
  align-items: center;
  gap: 15px;
  flex: 1;
  min-width: 0;
}
.chat-header .header-left .avatar-icon {
  font-size: 40px;
  width: 40px;
  height: 40px;
  color: #94a3b8;
  cursor: pointer;
  flex-shrink: 0;
  transition: all 0.3s ease;
}
.chat-header .header-left .avatar-icon:hover {
  opacity: 0.8;
  transform: scale(1.05);
}
.chat-header .header-left .avatar-icon.profile-pic {
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #334155;
}
.chat-header .header-left .chat-info {
  flex: 1;
  min-width: 0;
}
.chat-header .header-left .chat-info .chat-name {
  font-size: 16px;
  font-weight: 500;
  color: #94a3b8;
  line-height: 21px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.chat-header .header-right {
  display: flex;
  gap: 10px;
  align-items: center;
}
.chat-header .header-right .header-icon {
  color: #94a3b8;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat-header .header-right .header-icon lucide-angular {
  color: inherit;
}
.chat-header .header-right .header-icon:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #94a3b8;
  transform: scale(1.1);
}

.search-bar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: #0F172A;
  border-bottom: 1px solid #334155;
  height: 60px;
  flex-shrink: 0;
  transition: all 0.3s ease;
}
.search-bar .search-back-button {
  color: #94a3b8;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.3s ease;
  flex-shrink: 0;
}
.search-bar .search-back-button:hover {
  background: rgba(16, 185, 129, 0.1);
  color: #10B981;
}
.search-bar .search-input {
  flex: 1;
  background: #1E293B;
  border: 1px solid #334155;
  border-radius: 8px;
  padding: 10px 12px;
  color: #ffffff;
  font-size: 14.5px;
  font-family: inherit;
  outline: none;
  transition: all 0.3s ease;
}
.search-bar .search-input::placeholder {
  color: #64748b;
}
.search-bar .search-input:focus {
  border-color: #10B981;
  box-shadow: 0 0 0 2px rgba(16, 185, 129, 0.1);
  background: #0A0E27;
}
.search-bar .search-results-info {
  color: #94a3b8;
  font-size: 13px;
  font-weight: 500;
  white-space: nowrap;
  padding: 0 8px;
}
.search-bar .search-nav-button {
  color: #94a3b8;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  transition: all 0.3s ease;
  flex-shrink: 0;
}
.search-bar .search-nav-button:hover:not(:disabled) {
  background: rgba(16, 185, 129, 0.1);
  color: #10B981;
}
.search-bar .search-nav-button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.empty-state {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #0A0E27;
  background-image: repeating-linear-gradient(transparent, transparent 80px, rgba(52, 152, 219, 0.02) 80px, rgba(52, 152, 219, 0.02) 160px), repeating-linear-gradient(90deg, transparent, transparent 80px, rgba(52, 152, 219, 0.02) 80px, rgba(52, 152, 219, 0.02) 160px);
  border-left: 1px solid #334155;
}
.empty-state .empty-content {
  text-align: center;
  max-width: 460px;
  padding: 40px;
  animation: fadeIn 0.5s ease;
}
.empty-state .empty-content .empty-icon {
  margin-bottom: 32px;
}
.empty-state .empty-content .empty-icon svg {
  display: block;
  margin: 0 auto;
  opacity: 0.3;
}
.empty-state .empty-content h1 {
  font-size: 36px;
  font-weight: 300;
  color: #ffffff;
  margin: 0 0 24px 0;
  letter-spacing: -0.5px;
}
.empty-state .empty-content p {
  font-size: 14px;
  color: #64748b;
  line-height: 20px;
  margin: 0;
  font-weight: 400;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
